import React from "react";
import { NavLink } from "react-router-dom";
import data from "../data";
import uuid from "uuid";

const Navbar = props => {
  return (
    <nav className="navbar">
      {data.map(link => (
        <NavLink
          key={uuid.v4()}
          to={link.href}
          exact
          className="nav-link"
          activeClassName="active-link"
        >
          {link.title}
        </NavLink>
      ))}
    </nav>
  );
};

export default Navbar;
